}

#map-container {
  height: 35vh;
  width: 100%;
  background-color: #212121;
}

.chart {
  margin: 1em 0em;
}

.stats-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.stat {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 1em 0.5em;
  background-color: #212121;
  color: #ffffff;
}

.stat label {
  text-transform: uppercase;
  font-size: 3vw;
}

.stat span {
  color: #ff5349;
  font-size: 5vw;
}

.filters {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.tick {
  fill: #808080;
  font-size: 80%;
}

button {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #0e0e0e;
  color: #808080;
  border: none;
  padding: 0.5em;
  border-radius: 0.2em;
  cursor: pointer;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

button:focus {
  outline-color: #ffffff;
}

